<template>
  <div
    class="pa-2 px-2 mt-1 elevation-0"
    style="border-bottom: 1px dotted #ddd"
  >
    <div class="d-flex align-baseline">
      <span class="font-weight-bold overline">{{ code }}</span>
      <span
        class="body-1 ml-2"
      >
        {{ name }}
      </span>
    </div>
    <span>{{price}} &times; {{ realCount }} = {{price*realCount}}</span>
    <div v-if="hasMod>0">
      <dish-modification :dish-item="dishOrderDetail"/>
    </div>
    <div v-if="note">
      <v-icon>mdi-pencil</v-icon>{{note}}
    </div>
  </div>
</template>

<script>
  import DishModification from './DishModification'

  export default {
    name: 'DishLine',
    components: { DishModification },
    props: {
      // eslint-disable-next-line vue/require-default-prop
      dishOrderDetail: Object,
    },
    data: function () {
      return {
        ...this.dishOrderDetail,
      }
    },
    computed: {
      realCount: function () {
        return this.count ? this.count : this.sumCount
      },
    },
  }
</script>

