export async function salesInsight (timeSpan) {
  if (!timeSpan) {
    return []
  }
  if (!timeSpan.length || timeSpan.length < 2) {
    return []
  }
  const [timeStart, timeEnd] = timeSpan
  return hillo.get('Orders.php?op=saleAnalysis', {
    timeStart,
    timeEnd,
  })
}

